// Un servicio no es un componente, pero si una clase que puede tener decoradores. (Teor√≠a)
import { Entry } from './entry.model';
import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

@Injectable()
export class EntryService {

	// Contructor permite crear un instancia o una representacion del componente.
	constructor(private http: Http) {

	}

	// MET√ìDOS
	// Met√≥do getEntries para recuperar la fecha a trav√©s del Http Service. Retornar√° una Promesa que se resolver√° con un arreglo de 'entries'.
	// Para especificar el tipo de de dato que retornar√° el mm√©todo se agregan ':' despues de los parentesis del mismo.
	getEntries(): Promise<Entry[]> {
		return  this.http.get('/app/entries')
				.toPromise()
				.then(response => response.json().data as Entry[]);
	}
}


